@font-face {
  font-family: "Inter";
  src: url(/react-mesto-auth/static/media/Inter-Regular.3ac83020.woff) format("woff"), 
       url(/react-mesto-auth/static/media/Inter-Regular.dc131113.woff2) format("woff2");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  src: url(/react-mesto-auth/static/media/Inter-Black.661569af.woff2) format("woff2"), 
       url(/react-mesto-auth/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url(/react-mesto-auth/static/media/Inter-Medium.75db5319.woff2) format("woff2"), 
       url(/react-mesto-auth/static/media/Inter-Medium.c0638bea.woff) format("woff");
}

.page {
  font-family: "Inter", "Arial", "Helvetica", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  background-color: #000000;
  min-width: 320px;
  min-height: 100vh;
}

.button {
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  border-radius: 0;
  background: transparent;
  margin: 0;
  padding: 0;
  transition: opacity 0.3s ease;
}

.button:hover {
  opacity: 0.6;
  cursor: pointer;
}

.button:disabled {
  cursor: default;
  opacity: 0;
}
.header {
  max-width: 880px;
  padding: 45px 0 41px 0;
  margin: 0 auto;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 900px) {
  .header {
    width: calc(100% - 19px * 2);
  }
}

@media screen and (max-width: 528px) {
  .header {
    width: 100%;
    padding: 0;
  }
}

.header__logo {
  max-width: 142px;
  object-fit: cover;
}

@media screen and (max-width: 528px) {
   .header__logo {
    padding: 28px 0 31px 27px;
    max-width: 104px;
   }
 }
 
.content {
  max-width: 880px;
  margin: 0 auto;
}

@media screen and (max-width: 920px) {
  .content {
    margin: 0 10px;
  }
}

@media screen and (max-width: 528px) {
  .content {
    width: calc(100% - 19px * 2);
  }
}

.profile {
  display: flex;
  color: #fff;
  flex-wrap: wrap;
  padding-top: 40px;
}

@media screen and (max-width: 900px) {
  .profile {
    width: calc(100% - 19px * 2);
    margin: 0 auto;
  }
}

@media screen and (max-width: 528px) {
  .profile {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
}

.profile__avatar {
  border-radius: 50%;
  object-fit: cover;
  max-width: 120px;
  height: 120px;
}

.profile__info {
  max-width: 336px;
  width: 100%;
  margin-left: 30px;
  display: grid;
  align-self: center;
}

@media screen and (max-width: 528px) {
  .profile__info {
    margin: 26px 0 36px 0;
    max-width: 282px;
  }
}

.profile__title {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  grid-column: 1;
  grid-row: 1;
  max-width: 294px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 528px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 189px;
    margin: 0 auto;
  }
}

.profile__job {
  padding: 0;
  margin: 9px 0 0 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  grid-column: 1;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 528px) {
  .profile__job {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    margin: 0;
  }
}

.profile__edit-btn {
  background-image: url(/react-mesto-auth/static/media/EditButton.6bfdb354.svg);
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
  grid-column: 1;
  grid-row: 1;
  align-self: center;
  justify-self: flex-end;
  border: 1px solid #fff;
  background-position: center;
}

@media screen and (max-width: 528px) {
  .profile__edit-btn {
    width: 18px;
    height: 18px;
    margin-right: 19px;
  }
}

.profile__add-btn {
  background-image: url(/react-mesto-auth/static/media/AddButton.b2fce4ac.svg);
  background-repeat: no-repeat;
  width: 150px;
  height: 50px;
  background-position: center;
  margin-left: auto;
  border: 2px solid #fff;
  align-self: center;
}

@media screen and (max-width: 528px) {
  .profile__add-btn {
    width: 100%;
    background-size: 16px;
  }
}
.gallery {
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
   grid-column-gap: 17px;
   -webkit-column-gap: 17px;
           column-gap: 17px;
   grid-row-gap: 20px;
   row-gap: 20px;
   padding-top: 51px;
}

@media screen and (max-width: 528px) {
   .gallery {
      padding-top: 36px;
   }
}
.photo-card {
	max-width: 282px;
	width: 100%;
	background-color: #fff;
	border-radius: 10px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
  position: relative;
}

.photo-card__decription {
  display: flex;
  margin: 23px auto 23px auto;
  max-width: 246px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.photo-card__photo {
  display: block;
  width: 100%;
  height: 282px;
  object-fit: cover;
}

.photo-card__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.photo-card__like-btn {
   background-image: url(/react-mesto-auth/static/media/like.68fe0213.svg);
   max-width: 22px;
   height: 19px;
   width: 100%;
   background-repeat: no-repeat;
}

.photo-card__like-btn:active {
  background-image: url(/react-mesto-auth/static/media/heart_black.3d813f81.svg);
}
.footer {
  padding-top: 66px;
  padding-bottom: 60px;
  max-width: 880px;
  margin: 0 auto;
  box-sizing: border-box;
}

@media screen and (max-width: 900px) {
  .footer {
    width: calc(100% - 19px * 2);
  }
}

@media screen and (max-width: 528px) {
  .footer {
    padding-top: 48px;
    padding-bottom: 36px;
  }
}

.footer__copyright {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 528px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}

.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.5s ease;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
}

.popup__container {
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  color: #000;
  position: fixed;
  width: 430px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 540px) {
  .popup__container {
    width: 282px;
    padding: 25px 22px;
  }
}

.popup__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin: 0 0 54px 0;
}

@media screen and (max-width: 528px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 75px;
  }
}

.popup__title-confirm {
  margin: 0 0 20px 0;
}

.popup__close-btn {
  background-image: url(/react-mesto-auth/static/media/CloseIcon.cb0f2bbf.svg);
  width: 32px;
  height: 32px;
  position: absolute;
  top: -40px;
  right: -40px;
  background-size: cover;
}

@media screen and (max-width: 528px) {
  .popup__close-btn {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0px;
  }
}
.popup__container-photo {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #fff;
  position: relative;
}
.photo-card__like-btn_active {
  background-image: url(/react-mesto-auth/static/media/heart_black.3d813f81.svg);
}

.photo-card__remove-btn {
  background-image: url(/react-mesto-auth/static/media/Trash.4831282e.svg);
  width: 18px;
  height: 19px;
  background-size: cover;
  position: absolute;
  top: 20px;
  right: 20px;
}

.popup__figure {
  margin: 0;
  padding: 0;
}
.popup__photo {
  object-fit: cover;
  max-width: 75vw;
  max-height: 75vh;
}
.popup__caption {
  margin-top: 10px;
}

.popup_type_photo {
  background: rgba(0, 0, 0, 0.9);
}


.popup_opened {
  visibility: visible;
  opacity: 1;
}

.error {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #FF0000;
  min-height: 30px;
  box-sizing: border-box;
  display: block;
}

.photo-card__like-section {
  max-width: 22px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.photo-card__like-count {
  text-align: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.profile__picture-btn {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  background-image: url(/react-mesto-auth/static/media/edit_avatar.d476a8b8.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.profile__picture-btn:hover {
  opacity: 1;
}


.profile__picture {
  position: relative;
}

.profile__picture:hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.form {
  display: flex;
  flex-direction: column;
}

.form_type_login {
  width: 41%;
  min-width: 260px;
}

.form__input {
  height: 27px;
  width: 100%;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 0 13px 0;
  box-sizing: border-box;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.form__input:focus-visible {
  padding: 4px 0 4px 4px;
}
.form__submit-btn {
  height: 50px;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  background: #000;
  border-radius: 2px;
  width: 100%;
  margin: 18px 0 0 0;
}

@media screen and (max-width: 528px) {
  .form__submit-btn {
    height: 46px;
    font-size: 14px;
    line-height: 17px;
  }
}

.form__input_type_login {
  background-color: #000;
  border-bottom: 2px solid #ccc;
  color: #fff;
  margin: 0;
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__title {
  color: white;
  margin: 0;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  margin: 60px 0 50px;
}

@media screen and (max-width: 528px) {
  .login__title {
    font-size: 20px;
    line-height: 24px;
    margin: 40px 0 40px;
  }
}

.link {
  color: #fff;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.link:hover {
  opacity: 0.6;
  cursor: pointer;
}

.login__extra-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  margin: 15px 0 0;
}

.form__input_type_error {
  border-bottom: #FF0000 solid 1px;
}
.form__submit-btn_type_login {
  width: 100%;
  height: 50px;
  background-color: #fff;
  color: black;
  border-radius: 2px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin-top: 186px;
}

.form__submit-btn_disabled {
  background-color: rgb(170, 165, 165);
  color: rgb(116, 108, 108);
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.header__user-elements {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
}

.header__user-email {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  margin: 0 24px 0 0;
}

.header__logout {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #a9a9a9;
}

.header__nav-link {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
}

.popup__container-info-tooltip {
  align-items: center;
}
.popup__title_type_info-tooltip {
  margin-bottom: 23px;
  text-align: center;
}

.popup__image {
  max-width: 120px;
  max-height: 120px;
  margin: 26px 0 32px;
}



